import * as React from 'react';
import {Stage, Layer} from 'react-konva';

import { BoardField, FieldState, Point } from '../../utils/objectTypes';
import { colorNameToNum, colorNumToName } from '../../utils/functions';
import ScoreBar from './GameElements/ScoreBar';


function ScoreBoard(state: FieldState) {
    //temporarily hardcoded field size in pixels
    const canvasDimension = {X:state.dimension.X/3 * 15,Y:state.dimension.Y * 15};
    
    const scoreBars = createScoreBars(state, canvasDimension);
    return (
      <div className="ScoreBoard">
        <Stage width={canvasDimension.X} height={canvasDimension.Y} >
        <Layer>
          {scoreBars}
        </Layer>
      </Stage>
      </div>
    );
}

export default ScoreBoard;

// helpers

function createScoreBars(fields: FieldState, canvasDimension:Point){
  var bars: JSX.Element[] = [];
  
  var colorsArr, valuesArr: number[];
  ({colorsArr, valuesArr} = createHistogram(fields.fields));
  colorsArr.forEach((value: number, index: number) => {
    bars.push(<ScoreBar color={colorNumToName[value]} rect={scoreBoardLayouter(valuesArr, index, canvasDimension)} key={colorNumToName[value]}/>);
  });
  return bars;
}

function createHistogram(fields: BoardField[][]): {colorsArr: number[], valuesArr: number[]}{
  var colors: any = {};
  fields.forEach(row => {
    row.forEach(element => {
      if (element.color == colorNameToNum['white']){
        return;
      }
      if (colors.hasOwnProperty(element.color)){
        colors[element.color] += 1;
      } else {
        colors[element.color] = 1;
      }
    });
  });
  
  var colorsArr: any = Object.keys(colors);
  colorsArr.sort(function(a: any, b: any) {
    return parseFloat(colors[b]) - parseFloat(colors[a]);
  });
  var valuesArr = colorsArr.map((color: any) => colors[color]);
  return {colorsArr, valuesArr};
}

function scoreBoardLayouter(values: number[], position: number, dim: Point): {X: number, Y: number, Width: number, Height: number}{  
  return {X: 0, Y: Math.floor(dim.Y/values.length*position), Width: Math.floor(values[position]*dim.X/values[0]), Height: Math.floor(dim.Y/values.length), }
} 


